<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/11/01 23:14</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      下班了，很想你，很想跟你聊聊。一上公交车，找了个位置坐下后，就跟你打语音。可是几分钟后，我的心情又变得特别复杂，完全不想开口了，一直胡思乱想。你说你跟我在一起的机会已经很小了。我问为什么，你说因为他对你很好啊，我难受的不是因为他对你好，而是我在你心中地位似乎已经是可有可无的了。我已经不是你的第一选择了，能和我在一起，只能是别人对你不好的情况下。越想心里越复杂，越想心里越难过。这几天我一直找你，因为一直很想你，也很想我们能回到过去，可是发现你也许并没有那么想我，至少你没有我想你那么想你。前几天在网上给你买了一直狗狗的抱枕，看到今天已经签收了，但是也没听你提到过，下午我打了快递员的电话，我问快递是放在哪个地方你去取的还是本人亲自签收的，他说是本人签收的。可是怎么一直没听到你提及过，是我写错了信息还是你还没取，又或者是你取了也没看或者没想到会是我。可是我又不想亲自问你，我买那个抱枕，是想你看到他是能想到我，也是想让他在我没在的每一个夜晚能陪着你，希望你能每晚抱着他睡吧。
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



