<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/31 23:05</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      我还是很好奇，还是想知道你是不是在他家。我给你语音，你接了，当你接了的时候，我的心里石头仿佛落下来了，我以为是手机出现故障。可是聊着聊着，还是发现不对劲，我想跟你视频，你一度拒绝，我想看张现在的照片，你也是不同意。聊到后面，感觉到你已经不想跟我聊了。然后你截图说你手机没电了。本来我之前还在想，你的wifi是不是你用手机开的热点。可是看到你的截图，用的是双卡手机，怎么给自己手机开wifi，后面你用平板看电视，我问你开的热点？你也答得有点心虚的样子，后面我还是说想看看现在的你，可是你仍然拒绝，你说你要睡了，而且灯也关了，如果你真的一个人在床上耍手机，你不会关灯吧，你都是坐着，等到不耍了再去上个厕所然后关灯睡觉。然后你没跟我聊了，一晚上，我睡不着，拿着手机也不知道干什么，后面无意间听到一首歌，深有感触，然后单曲循环——《一个人的北京》。后面不知道过了多久才睡着。我想要的照片也是在第二天才给我。我知道，如果你真的在他家你们也是睡得不同房间，可是我的心里还是难以接受。只有我仍然在傻傻地等，傻傻地等你回来，可是你离他越来越近，离我已经越来越远了。
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



