<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/26 05:42</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      完全睡不着，从昨晚到到现在又是一个通宵未眠，真的是伤心得一塌糊涂。我好想回到过去，好好珍惜你，我好想你选择的是我。难道我们真的就这样结束了？我也不知道我这样让你在这个时候做出选择是对是错，是不是应该像前两天那样跟你依然保持着联系，这样又知道了你的动态，等你回来的可能性也就越大。只是那样真的让我好痛好痛，因为我无法忍受我深爱的人此时此刻却和别人在一起，我真的做不到。但是我又很怕，我怕你以为真的失去我了，然后你会渐渐的不想我了，你渐渐的真的接受他了，你们真的在一起了，在一起生活了，这样我就真的没戏了。我也不知道我在胡思乱想什么，明明你选择了他，却还盼着你能回来。
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



