<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/28 22:06</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      和往常一样，除了想你还是想你，不过今晚我和我爸爸吃饭的时候，他问我有没有钱，他说要交房贷了。虽然之前我听他说了很多次，不是对我，而是对我姐姐。他说这话，我想了很多，一直以来，除了爸妈，我姐姐也给这房子出了不少力，而我一直都在用钱。现在毕业了，工作了。却没有什么能力为家庭做出什么贡献。我问我爸差多少，她说看姐姐给多少，最后他问我有没有400，虽然我的工资很少，但是现在身上一两千还是拿得出手的。本来打算存点钱的，可我发现，就算我再节俭，每个月这少得可怜的工资又怎么存得了呢，再加上年底有朋友结婚，又得拿出一笔，所以存钱是不可能存的。我想到你离开我你也是没错的，如果和我在一起，至少在这一两年里，给不了你物质上太多好的东西。所以我突然也不怪你了，我只怪我自己，怪自己没有那么大能力，现在照顾自己都有些困难，又怎么能好好照顾你呢，也希望你能真正的幸福。
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



