<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/25 21:30</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      下午的时候，当知道你在他家耍之后，我的心理特别难受。我不知道我该干什么，上班的时候眼睛盯着屏幕发呆，看不进去任何东西，也写不了任何代码。我该怎么做才能让我不去那么想你。真的好想好想你，可是想得越多，带来的失落就越大。我在想是不是断去联系，然后让时间来让我不那么想你，可是很舍不得，是不是应该让自己狠下来，哪怕再想也要忍住不去找你的，这样可能刚开始很想很想，时间久了就不会那么想了。我也感到好迷茫，完全不知道该怎么办，本身要加班的我我跑回来了，回到家，我想了很多。特别不想失去你，我想再争取一下，我想你回到我的身边，我给你在QQ上说了我的想法，我想要一个答案，我更想要我想要的答。我随时准备好了，我决定只要你说你愿意再给我一次机会，我就会马上跑到你身边，然后以后的以后，我会认真对待这份感情，好好对你，好好爱你，好好疼你。可是你一句对不起，让我伤心的不能再伤心了。我多么希望你能再给我一次机会...
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
    <div class="item-img-box-2">
      <div class="item-img-2"><img src="../../assets/images/2018102501.jpg" alt=""></div>
      <div class="item-img-2"><img src="../../assets/images/2018102502.jpg" alt=""></div>
      <div class="item-img-2"><img src="../../assets/images/2018102503.jpg" alt=""></div>
      <div class="item-img-2"><img src="../../assets/images/2018102504.jpg" alt=""></div>
    </div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



