<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/27 21:33</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      我以为可以借助酒精能麻痹自己神经，昨晚喝了很多酒，可是发现根本没用，仍然很想你。我不想就这样失去你了，我想你回来，可是，我还是输给了现实。我也不知道我该怎么办，很烦。不想工作，什么都不想。我好害怕你们同居，这样的话，你可能是真的放弃我了。
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



