<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/30 21:00</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      今天早早回家，同事们都在加班，可是我不是很想加班。回到家，也发现什么都不想干。其实昨晚，他请你们同事吃饭，我心里还是有很多想法的，我问你回家没有，你说回去了。但是你知道吗，我的直觉告诉我，你并没有回去。我给你语音，本来想跟你多聊一下，有很多话跟你说，可是你说你眼镜摔了，眼睛有点累，想睡觉。挂了语音，我怎么也睡不着。看到你的QQ是wifi在线，就让我胡思乱想。但是我心里还是默默安慰自己，可能是手机显示有问题。然后我一天就一直关注了，今天上班时，我用电脑登着，不停的看，我发现一直都是wifi在线，知道上午大概十点以后，变成了4G，我知道你去上班了，所以让我难以想象你昨晚是在家了。晚上下班了，我想找你聊，你说你吃饭，我就想等会吧，你应该是和他一起吃的。所以也就没有打扰你了。一直到现在，晚上9点，我再次关注你的时候，我发现又变成了WiFi在线了。我的心里泛起波澜。好难受...我不知道我是该继续找你还是假装没看到，假装没看到是不可能的了，我只是不知道该不该继续找你。
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



